@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
html{font-size:1.56vw;background-color:#e5e5e5}@media (min-width: 1440px){html{font-size:14px}}

.tickets-template{margin-top:1.66rem}.ticket{display:flex;flex-direction:row;align-items:center;justify-content:space-between;width:100%;flex-wrap:wrap;padding:1.66rem;background:#ffffff;box-shadow:0px 2px 8px rgba(0,0,0,0.1);border-radius:5px;margin-bottom:1.66rem}.ticket p{margin:0}.ticket .ticket__price{width:50%;font-weight:600;font-size:2rem;line-height:2rem;color:#2196f3}.ticket .ticket__carrier{width:25%;background-repeat:no-repeat;background-position:center;background-size:100%;margin-right:5%}.ticket .segment{width:100%;display:flex;flex-direction:row;align-items:center;justify-content:space-between;padding-top:.83rem}.ticket .segment .flight,.ticket .segment .duration,.ticket .segment .stops{width:30%;font-size:1rem}.ticket .segment .flight .title,.ticket .segment .duration .title,.ticket .segment .stops .title{color:#A0B0B9}.ticket .segment .flight .info,.ticket .segment .duration .info,.ticket .segment .stops .info{color:#4A4A4A}

.tickets-template{width:100%;display:flex;flex-direction:column;justify-content:flex-start;align-items:center}.tabs{width:100%;display:flex;flex-direction:row;justify-content:center;align-items:center;font-size:0.9rem;padding:0}

.tab{width:50%;border:none;background:#FFFFFF;color:#4A4A4A;letter-spacing:calc(1rem/28);border:1px solid #DFE5EC;box-sizing:border-box;font-size:0.85rem;line-height:3.5rem;padding:0}.selected{background:#2196F3;letter-spacing:calc(1rem/28);color:#FFFFFF;border:none}#cheap{border-radius:0.25rem 0 0 0.25rem}#fast{border-radius:0 0.25rem 0.25rem 0}

.filter_option{font-size:1.25rem;height:3.5rem;display:flex;flex-direction:row;align-items:center;justify-content:space-between;width:100%}.filter_option:hover{background:#F1FCFF}.filter_option .control-checkbox{position:relative;right:-1.5rem}*,*:before,*:after{box-sizing:border-box;margin:0;padding:0}.filter_option input{padding:0;height:auto;height:initial;width:auto;width:initial;margin-bottom:0;display:none;cursor:pointer}.filter_option label{position:relative;cursor:pointer}.filter_option label:before{content:'';-webkit-appearance:none;background-color:transparent;border:1px solid #0079bf;box-shadow:0 1px 2px rgba(0,0,0,0.05),inset 0px -15px 10px -12px rgba(0,0,0,0.05);padding:0.75rem;border-radius:0.2rem;display:inline-block;position:relative;vertical-align:middle;cursor:pointer;margin-right:0.6rem}.filter_option input:checked+label:after{content:'';display:block;position:absolute;top:0.4rem;left:0.6rem;width:0.6rem;height:1rem;border:solid #0079bf;border-width:0 0.15rem 0.15rem 0;-webkit-transform:rotate(45deg);transform:rotate(45deg)}

.filter{display:flex;flex-direction:column;align-items:flex-start;justify-content:center;width:26%;background:#FFFFFF;box-shadow:0px 2px 8px rgba(0,0,0,0.1);border-radius:5px;padding-top:1.66rem;padding-bottom:1.66rem}.filter .filter_title{font-size:1.25rem;color:#4A4A4A;text-indent:1.66rem}

.App{display:flex;flex-direction:row;justify-content:space-around;align-items:flex-start;flex-wrap:wrap}html,body,#root,.App{margin:0;padding:0;width:100vw;width:100%;font-family:"Open Sans";font-style:normal}@media (min-width: 1440px){.App{width:900px;margin:0 auto}}.main{width:56%;display:flex;align-items:center;flex-direction:column;justify-content:flex-start}.App_logo{width:7vw;height:7vw;background-size:100%;background-repeat:no-repeat;background-position:center;padding-left:40%;padding-right:40%;padding-top:3%;padding-bottom:3%}

